getsellToken.prototype.xxx="sssss";
getsellToken.prototype.addnft= function(Nftcontractaddress,tokenId,Price){
      // NFT
      const tokenInfo = {
        address: Nftcontractaddress,
        tokenId: tokenId.toString()
    };

    // tokenInfo
    const data = ethabi.encodeParameter(
        'tuple(address, uint256)[]',
      [[tokenInfo.address, tokenInfo.tokenId]]
    );

    // tokenId1NFT, 1ETH
    const sellToken = {
        price: toWei(Price),
        data: data
      };
      this.list.push(sellToken);
      this.originallist.push(tokenInfo);
};

getsellToken.prototype.addnftcollection=function(Nftcontractaddress,Price){
      // collectionoffer, tokenId0
      const collectionTokenInfo = {
        address: Nftcontractaddress,
        tokenId: '0'
      };
  
      
      const data = ethabi.encodeParameter(
          'tuple(address, uint256)[]',
        [[collectionTokenInfo.address, collectionTokenInfo.tokenId]]
      );
  
      const buyToken = {
        price: toWei(Price),
        data: data
      };
      this.list=[];
      this.originallist=[];
      this.list.push(buyToken);
      this.originallist.push(collectionTokenInfo);

};

getsellToken.prototype.getList= function(){
  return this.list;
};

getsellToken.prototype.getListforServer= function(){
  if(this.originallist.length!=this.list.length){
    console.log('need originallist and list');
    return;
  }
  const items=[];
   for (let index = 0; index < this.originallist.length; index++) {
     const element = this.originallist[index];
     items.push( {
      "contract_address": element.address,
      "token_id": element.tokenId,
      "price": this.list[index].price
     });
     
   }
  
  return items;
};

